<template>
  <div>
    <div class="headerTextBdc">Search Blood Donation Club</div>
    <b-row class="mt-2">
      <b-col cols="12" xl="6" id="options">
        <label>Select Blood Group</label>
        <b-form-select
          v-model="selectedBloodGroup"
          :options="bloodGroupOption"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
      <b-col id="options" cols="12" xl="6" class="justifyMobile">
        <label>Select Country</label>
        <b-form-select
          v-model="selectedCountry"
          :options="CountryOption"
          @change="getStateData"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
    </b-row>
    <br />
    <b-row>
      <b-col id="options" cols="12" xl="4">
        <label>Select State</label>
        <b-form-select
          v-model="selectedState"
          :options="StateOption"
          @change="getDistData"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
      <b-col id="options" cols="12" xl="4" class="justifyMobile">
        <label>Select District</label>
        <b-form-select
          v-model="selectedDist"
          :options="DistOption"
          @change="getCityData"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
      <b-col id="options" cols="12" xl="4" class="justifyMobile">
        <label>Select City</label>
        <b-form-select
          v-model="selectedCity"
          :options="CityOption"
          value-field="item"
          text-field="name"
          disabled-field="notEnabled"
        >
        </b-form-select>
      </b-col>
    </b-row>
    <br />
    <b-row>
      <b-col class="d-flex justify-content-center text-center">
        <b-button @click="getDonarData" variant="primary">Search</b-button>
      </b-col>
    </b-row>
    <div class="mt-5">
      <b-row>
        <b-col cols="6" xl="6">
          <label>User List</label>
        </b-col>
        <b-col cols="6" xl="6" class="float-right">
          <b-button
            @click="showRegisterModal"
            variant="primary"
            class="float-right"
            >Register User</b-button
          >
        </b-col>
      </b-row>
    </div>
    <div class="mt-4">
      <b-table
        id="selectableTable"
        ref="selectableTable"
        :key="indexRefresh"
        :busy="isBusy"
        :select-mode="selectModeTable"
        :fields="fields"
        :items="DonarData"
        :striped="true"
        :bordered="false"
        :borderless="false"
        head-variant="dark"
        responsive
        sticky-header
      >
        <template v-slot:cell(BloodGroup)="item">{{ item.value }}</template>
        <template v-slot:cell(Name)="item">{{ item.value }}</template>
        <template v-slot:cell(Email)="item">{{ item.value }}</template>
        <template v-slot:cell(Address)="item">{{ item.value }}</template>
      </b-table>
      <b-modal
        id="tree-modal1"
        ref="tree-modal1"
        :hide-footer="true"
        centered
        variant="secondary"
        modal-class="py-0"
        body-class="py-0"
        size="lg"
        footer-class="border-1"
        header-class="border-1 bg-white pd-0"
        button-size="sm"
        hide-header-close
      >
        <b-card class="pop-up-box border-0">
          <b-row>
            <b-col cols="12" xl="4">
              <label>first name</label>
              <input
                v-model="RegisterInfo.first_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.first_name"
                :field="$t('first_name')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>last name</label>
              <input
                v-model="RegisterInfo.last_name"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.last_name"
                :field="$t('last_name')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Phone no</label>
              <input v-model="RegisterInfo.phone" class="form-control" />
              <show-errors
                :control="$v.RegisterInfo.phone"
                :field="$t('phone')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Email</label>
              <input
                v-model="RegisterInfo.email"
                type="text"
                class="form-control"
              />
              <show-errors
                :control="$v.RegisterInfo.email"
                :field="$t('email')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Select Blood Group</label>
              <b-form-select
                v-model="RegisterInfo.selected_bloodgroup"
                :options="bloodGroup_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_bloodgroup"
                :field="$t('bloodgroup')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Address</label>
              <input v-model="RegisterInfo.address" class="form-control" />
              <show-errors
                :control="$v.RegisterInfo.address"
                :field="$t('address')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Select Country</label>
              <b-form-select
                v-model="RegisterInfo.selected_Country"
                :options="Country_Option"
                @change="getStateDetails"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_Country"
                :field="$t('Country')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Select State</label>
              <b-form-select
                v-model="RegisterInfo.selected_State"
                :options="State_Option"
                @change="getDistDetails"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_State"
                :field="$t('State')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Select District</label>
              <b-form-select
                v-model="RegisterInfo.selected_District"
                :options="District_Option"
                @change="getCityDetails"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_District"
                :field="$t('District')"
              />
            </b-col>
          </b-row>
          <b-row class="mt-3">
            <b-col cols="12" xl="4">
              <label>Select City</label>
              <b-form-select
                v-model="RegisterInfo.selected_City"
                :options="City_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.selected_City"
                :field="$t('City')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>Is Plasma Donar</label>
              <b-form-select
                v-model="RegisterInfo.plasmadonar"
                :options="plasmadonar_Option"
                value-field="item"
                text-field="name"
                disabled-field="notEnabled"
              >
              </b-form-select>
              <show-errors
                :control="$v.RegisterInfo.plasmadonar"
                :field="$t('plasmadonar')"
              />
            </b-col>
            <b-col cols="12" xl="4">
              <label>PinCode</label>
              <input v-model="RegisterInfo.pincode" class="form-control" />

              <show-errors
                :control="$v.RegisterInfo.pincode"
                :field="$t('PinCode')"
              />
            </b-col>
          </b-row>
        </b-card>
        <b-row>
          <b-col cols="12" class="mb-2 d-flex justify-content-end">
            <b-button @click="close" variant="secondary" class="mr-1"
              >close</b-button
            >
            <b-button @click="RegisterData" variant="primary">Add</b-button>
          </b-col>
        </b-row>
      </b-modal>
    </div>
  </div>
</template>

<script>
import { Settings } from "luxon";
import { required, email, numeric, minLength } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
Settings.defaultLocale = "ja";
import axios from "axios";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");

export default {
  name: "Index",
  components: { ShowErrors },
  data() {
    return {
      indexRefresh: 1,
      selectedBloodGroup: "",
      selectedCountry: "",
      selectedState: "",
      selectedDist: "",
      selectedCity: "",
      bloodGroupOption: [],
      CountryOption: [],
      StateOption: [],
      DistOption: [],
      CityOption: [],
      DonarData: [],
      tableData: [],
      countryData: [],
      bloodGroupData: [],
      bloodGroup_Option: [],
      Country_Option: [],
      State_Option: [],
      District_Option: [],
      City_Option: [],
      selectModeTable: "single",
      plasmadonar_Option: [
        { item: "0", name: "Yes" },
        { item: "1", name: "No" },
      ],
      RegisterInfo: {
        first_name: "",
        last_name: "",
        phone: "",
        email: "",
        selected_bloodgroup: "",
        selected_Country: "",
        selected_State: "",
        selected_District: "",
        selected_City: "",
        plasmadonar: "",
        address: "",
        pincode: "",
      },
    };
  },
  computed: {
    fields() {
      return [
        {
          key: "BloodGroup",
          label: "BloodGroup",
          class: "text-center normal-width",
        },
        {
          key: "Name",
          label: "Name",
          class: "text-center normal-width",
        },
        {
          key: "Email",
          label: "Email",
          class: "text-center normal-width",
        },
        {
          key: "Address",
          label: "Address",
          class: "text-center normal-width",
        },
      ];
    },
  },
  validations: {
    RegisterInfo: {
      first_name: { required },
      last_name: { required },
      phone: { required, phoneNumber: numeric, phoneMinLength: minLength(10) },
      email: { required, email },
      selected_bloodgroup: { required },
      selected_Country: { required },
      selected_State: { required },
      selected_District: { required },
      selected_City: { required },
      plasmadonar: { required },
      address: { required },
      pincode: { required },
    },
  },
  mounted() {
    this.serviceGetFunction();
    this.getBloodGroupData();
    this.getCountryData();
  },
  created() {},
  methods: {
    async RegisterData() {
      this.$v.RegisterInfo.$touch();
      if (!this.$v.RegisterInfo.$invalid) {
        let payLoad = [
          {
            table: "AddressBook",
            primary_key: "id",
            data: [
              {
                address: this.RegisterInfo.address,
                city_id: this.RegisterInfo.selected_City,
                pincode: this.RegisterInfo.pincode,
              },
            ],
            dependency: [],
          },
          {
            table: "User",
            primary_key: "id",
            data: [
              {
                first_name: this.RegisterInfo.first_name,
                last_name: this.RegisterInfo.last_name,
                email: this.RegisterInfo.email,
                phone: this.RegisterInfo.phone,
                blood_group_id: this.RegisterInfo.selected_bloodgroup,
                password: "123456789",
                is_plasma_donor: this.RegisterInfo.plasmadonar,
              },
            ],
            dependency: [
              {
                entity_name: "AddressBook",
                entity_primary: "id",
                entity_foreign: "address_book_id",
              },
            ],
          },
        ];
        let url = `mastermodel/createinmultitablewithdependentids`;
        let Data = await BasicInfoApis.postAPI(url, payLoad);
        console.log(Data, "DataData");
        if (Data.data.success) {
          this.serviceGetFunction();
          this.$refs["tree-modal1"].hide();
        }
      }
    },
    showRegisterModal() {
      this.$refs["tree-modal1"].show();
    },
    close() {
      this.$refs["tree-modal1"].hide();
    },
    getDonarData() {
      let payload = {
        paginate: false,
        limit: 100,
        fieldlist: [
          "users.id",
          "users.first_name",
          "users.last_name",
          "users.email",
          "users.phone",
          "blood_groups.blood_type",
          "address_books.address",
          "cities.city_name",
          "districts.district_name",
          "states.state_name",
          "states.id as state_id",
          "countries.country_name",
          "countries.dial_code",
          "countries.id as country_id",
          "address_books.pincode",
          "address_books.id as address_book_id",
          "users.blood_group_id",
          "address_books.city_id",
          "districts.id as district_id",
        ],
        joins: [
          {
            table: "blood_groups",
            join_type: "left",
            join_conditions: [
              {
                join_using: "blood_groups.id",
                join_operator: "=",
                join_with: "users.blood_group_id",
              },
            ],
          },
          {
            table: "address_books",
            join_type: "left",
            join_conditions: [
              {
                join_using: "address_books.id",
                join_operator: "=",
                join_with: "users.address_book_id",
              },
            ],
          },
          {
            table: "cities",
            join_type: "left",
            join_conditions: [
              {
                join_using: "cities.id",
                join_operator: "=",
                join_with: "address_books.city_id",
              },
            ],
          },
          {
            table: "districts",
            join_type: "left",
            join_conditions: [
              {
                join_using: "districts.id",
                join_operator: "=",
                join_with: "cities.district_id",
              },
            ],
          },
          {
            table: "states",
            join_type: "left",
            join_conditions: [
              {
                join_using: "districts.state_id",
                join_operator: "=",
                join_with: "states.id",
              },
            ],
          },
          {
            table: "countries",
            join_type: "left",
            join_conditions: [
              {
                join_using: "countries.id",
                join_operator: "=",
                join_with: "states.country_id",
              },
            ],
          },
        ],
        conditions: [
          {
            key: "countries.id",
            operator: "=",
            value: this.selectedCountry,
          },
          {
            key: "states.id",
            operator: "=",
            value: this.selectedState,
          },
          {
            key: "districts.id",
            operator: "=",
            value: this.selectedDist,
          },
          {
            key: "cities.id",
            operator: "=",
            value: this.selectedCity,
          },
          {
            key: "users.blood_group_id",
            operator: "=",
            value: this.selectedBloodGroup,
          },
        ],
      };
      let url = `https://119.82.96.67:8082/api/master/readwithjoin/users`;
      this.serviceFunction(payload, url);
    },
    async getBloodGroupData() {
      //http://119.82.96.67:8082/api/master/readall/countries
      let url = `master/readall/blood_groups`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.bloodGroupOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      this.bloodGroup_Option.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      this.bloodGroupData = data.data;
      for (let index = 0; index < this.bloodGroupData.length; index++) {
        this.bloodGroupOption.push({
          item: this.bloodGroupData[index].id,
          name: this.bloodGroupData[index].blood_type,
        });
      }
      for (let index = 0; index < this.bloodGroupData.length; index++) {
        this.bloodGroup_Option.push({
          item: this.bloodGroupData[index].id,
          name: this.bloodGroupData[index].blood_type,
        });
      }
      console.log("this.bloodGroupOption", this.bloodGroupOption);
    },
    async getCountryData() {
      //http://119.82.96.67:8082/api/master/readall/countries
      let url = `master/readall/countries`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.countryData = data.data;
      this.CountryOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      this.Country_Option.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      //Country_Option
      for (let index = 0; index < this.countryData.length; index++) {
        this.CountryOption.push({
          item: this.countryData[index].id,
          name: this.countryData[index].country_name,
        });
      }
      for (let index = 0; index < this.countryData.length; index++) {
        this.Country_Option.push({
          item: this.countryData[index].id,
          name: this.countryData[index].country_name,
        });
      }
    },
    async getStateDetails() {
      this.State_Option = [];
      console.log(this.RegisterInfo, "KKKKKK");
      alert(
        "this.RegisterInfo.selected_Country",
        this.RegisterInfo.selected_Country
      );
      let url = `getstates?country_id=${this.RegisterInfo.selected_Country}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      let StateData = data;
      console.log("StateData", StateData.length);
      this.StateOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.State_Option.push({
          item: data[index].id,
          name: data[index].state_name,
        });
      }
    },
    async getStateData() {
      this.StateOption = [];
      console.log(this.selectedCountry, "selectedCountry");
      let url = `getstates?country_id=${this.selectedCountry}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      let StateData = data;
      console.log("StateData", StateData.length);
      this.countryData = data.data;
      this.StateOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.StateOption.push({
          item: data[index].id,
          name: data[index].state_name,
        });
      }
    },
    async getDistDetails() {
      this.District_Option = [];
      let url = `getdistricts?state_id=${this.RegisterInfo.selected_State}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.District_Option.push({
        item: "0",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.District_Option.push({
          item: data[index].id,
          name: data[index].district_name,
        });
      }
    },
    async getDistData() {
      this.DistOption = [];
      let url = `getdistricts?state_id=${this.selectedState}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.DistOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.DistOption.push({
          item: data[index].id,
          name: data[index].district_name,
        });
      }
    },
    async getCityDetails() {
      this.City_Option = [];
      let url = `getcities?district_id=${this.RegisterInfo.selected_District}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.City_Option.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.City_Option.push({
          item: data[index].id,
          name: data[index].city_name,
        });
      }
    },
    async getCityData() {
      this.CityOption = [];
      let url = `getcities?district_id=${this.selectedDist}`;
      const { data } = await BasicInfoApis.getAPI(url);
      console.log(data, "data2");
      this.CityOption.push({
        item: "",
        name: "Select",
        disabled: true,
      });
      for (let index = 0; index < data.length; index++) {
        this.CityOption.push({
          item: data[index].id,
          name: data[index].city_name,
        });
      }
    },
    async serviceGetFunction() {
      let url = "https://119.82.96.67:8082/api/master/readall/users";
      let data = await axios.get(url);
      console.log(data, "data");
      if (data.status == "200") {
        this.tableData = data.data.data;
        console.log(this.tableData, "DATTATATATAT");
      }
      let AllData;
      let newData = [];
      this.tableData.forEach((data) => {
        AllData = {
          BloodGroup: data.blood_type, //blood_type,
          Name: data.first_name,
          Phone: data.phone,
          Address: data.address,
          Email: data.email,
        };
        this.DonarData.push(AllData);
      });
      this.indexRefresh = this.indexRefresh + 1;

      return data;
    },
    async serviceFunction(payLoad, url) {
      this.DonarData = [];
      let data = await axios.post(url, payLoad, {
        headers: {
          //   "Access-Control-Allow-Origin": true,
          //   Accept: "application/json",
          //   "Content-Type": "application/json",
        },
      });
      console.log(data, "data");
      if (data.data.success) {
        this.tableData = data.data.data.data;
      }
      let AllData;
      let newData = [];
      this.tableData.forEach((data) => {
        AllData = {
          BloodGroup: data.blood_type,
          Name: data.first_name,
          Phone: data.phone,
          Address: data.address,
          Email: data.email,
        };
        this.DonarData.push(AllData);
      });
      this.indexRefresh = this.indexRefresh + 1;

      return data;
    },
  },
};
</script>
<style>
.dashBoardFrmClass {
  background-color: #fff;
  border: none;
  border-radius: 8px;
  box-shadow: 0 0px 0px rgb(0 0 0 / 10%), 0 8px 16px rgb(0 0 0 / 10%);
  box-sizing: border-box;
}
@media only screen and (max-width: 773px) {
  .justifyMobile {
    margin-top: 15px !important;
  }
}
</style>
